.home {
  @apply font-homePrimary;
}

.home * {
  @apply font-[inherit];
}

.footer {
  @apply bg-[#F0F2F5];
  min-height: calc(100vh - 56px);
}

li {
  @apply flex flex-row items-center cursor-pointer mb-2 w-[20%] p-[0.5rem] rounded-[6px];
}

li:hover {
  @apply bg-[#dbdee3];
}
